import { supabase } from '../lib/supabase';

interface EmailRequest {
  to: string;
  subject: string;
  template: 'verification' | 'welcome';
  data: {
    name: string;
    verificationCode?: string;
    [key: string]: any;
  };
}

// Supabase Edge Function을 통한 이메일 발송
export const sendEmailViaSupabase = async (
  emailRequest: EmailRequest
): Promise<{ success: boolean; message: string }> => {
  try {
    console.log('📧 Supabase Edge Function으로 이메일 발송 시작...');
    
    const { data, error } = await supabase.functions.invoke('send-email', {
      body: emailRequest,
    });

    if (error) {
      throw error;
    }

    console.log('✅ Supabase 이메일 발송 성공:', data);
    return {
      success: true,
      message: '이메일이 성공적으로 발송되었습니다.',
    };
  } catch (error: any) {
    console.error('❌ Supabase 이메일 발송 실패:', error);
    return {
      success: false,
      message: `이메일 발송에 실패했습니다: ${error.message}`,
    };
  }
};

// 인증번호 이메일 발송 (Supabase)
export const sendVerificationEmailSupabase = async (
  email: string,
  name: string,
  verificationCode: string
): Promise<{ success: boolean; message: string }> => {
  return sendEmailViaSupabase({
    to: email,
    subject: 'AI LMS 이메일 인증번호',
    template: 'verification',
    data: {
      name,
      verificationCode,
    },
  });
};

// 환영 이메일 발송 (Supabase)
export const sendWelcomeEmailSupabase = async (
  email: string,
  name: string
): Promise<{ success: boolean; message: string }> => {
  return sendEmailViaSupabase({
    to: email,
    subject: 'AI LMS에 오신 것을 환영합니다!',
    template: 'welcome',
    data: {
      name,
      loginUrl: `${window.location.origin}/login`,
    },
  });
};

// Edge Function 코드 (supabase/functions/send-email/index.ts)
export const EDGE_FUNCTION_CODE = `
import { serve } from "https://deno.land/std@0.168.0/http/server.ts"
import { createClient } from 'https://esm.sh/@supabase/supabase-js@2'

const corsHeaders = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Headers': 'authorization, x-client-info, apikey, content-type',
}

serve(async (req) => {
  // CORS 처리
  if (req.method === 'OPTIONS') {
    return new Response('ok', { headers: corsHeaders })
  }

  try {
    const { to, subject, template, data } = await req.json()
    
    // 이메일 템플릿 생성
    const htmlContent = generateEmailTemplate(template, data)
    
    // 이메일 발송 (SendGrid, AWS SES 등 사용)
    const emailResponse = await sendEmail({
      to,
      subject,
      html: htmlContent,
    })

    return new Response(
      JSON.stringify({ success: true, message: '이메일 발송 완료' }),
      {
        headers: { ...corsHeaders, 'Content-Type': 'application/json' },
        status: 200,
      },
    )
  } catch (error) {
    return new Response(
      JSON.stringify({ error: error.message }),
      {
        headers: { ...corsHeaders, 'Content-Type': 'application/json' },
        status: 400,
      },
    )
  }
})

function generateEmailTemplate(template: string, data: any): string {
  if (template === 'verification') {
    return \`
      <html>
        <body style="font-family: Arial, sans-serif;">
          <div style="max-width: 600px; margin: 0 auto; padding: 20px;">
            <h1>이메일 인증</h1>
            <p>안녕하세요, \${data.name}님!</p>
            <p>인증번호: <strong>\${data.verificationCode}</strong></p>
            <p>이 인증번호는 5분 후에 만료됩니다.</p>
          </div>
        </body>
      </html>
    \`
  }
  
  if (template === 'welcome') {
    return \`
      <html>
        <body style="font-family: Arial, sans-serif;">
          <div style="max-width: 600px; margin: 0 auto; padding: 20px;">
            <h1>환영합니다!</h1>
            <p>안녕하세요, \${data.name}님!</p>
            <p>AI LMS에 가입해주셔서 감사합니다.</p>
            <a href="\${data.loginUrl}">로그인하러 가기</a>
          </div>
        </body>
      </html>
    \`
  }
  
  return '<p>이메일 템플릿을 찾을 수 없습니다.</p>'
}

async function sendEmail({ to, subject, html }: { to: string; subject: string; html: string }) {
  // 실제 이메일 서비스 API 호출
  // 예: SendGrid, AWS SES, Mailgun 등
  
  // SendGrid 예시:
  const response = await fetch('https://api.sendgrid.com/v3/mail/send', {
    method: 'POST',
    headers: {
      'Authorization': \`Bearer \${Deno.env.get('SENDGRID_API_KEY')}\`,
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      personalizations: [{
        to: [{ email: to }],
      }],
      from: { email: 'noreply@ai-lms.com' },
      subject,
      content: [{
        type: 'text/html',
        value: html,
      }],
    }),
  })
  
  return response.json()
}
`;