import { supabase, type Database, type Course, type UserProfile, type Enrollment, type SubscriptionPlan } from '../lib/supabase'

// 인증 관련 API
export const authApi = {
  // 로그인
  async signIn(email: string, password: string) {
    console.log('🔄 Supabase signIn 시도:', { email });
    
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password,
    })
    
    if (error) {
      console.error('❌ Supabase signIn 실패:', error);
      
      // 사용자가 존재하는지 확인
      try {
        const { data: profile } = await supabase
          .from('user_profiles')
          .select('*')
          .eq('email', email)
          .single();
          
        if (profile) {
          console.log('📋 프로필은 존재함:', profile);
        } else {
          console.log('❌ 프로필이 존재하지 않음');
        }
      } catch (profileError) {
        console.error('❌ 프로필 확인 중 오류:', profileError);
      }
      
      return { data, error };
    }
    
    console.log('✅ Supabase signIn 성공:', data);
    
    // 로그인 성공 시 사용자 프로필 정보 가져오기
    if (data.user) {
      try {
        const { data: profile, error: profileError } = await supabase
          .from('user_profiles')
          .select('*')
          .eq('id', data.user.id)
          .single();
          
        if (profileError) {
          console.error('❌ 프로필 조회 실패:', profileError);
        } else {
          console.log('✅ 프로필 조회 성공:', profile);
          // 사용자 정보에 프로필 정보 추가
          const userWithProfile = {
            ...data.user,
            role: profile.role,
            first_name: profile.first_name,
            last_name: profile.last_name,
            profile: profile
          };
          return { 
            data: { 
              ...data, 
              user: userWithProfile 
            }, 
            error: null 
          };
        }
      } catch (profileError) {
        console.error('❌ 프로필 조회 중 오류:', profileError);
      }
    }
    
    return { data, error }
  },

  // 회원가입
  async signUp(email: string, password: string, metadata?: { 
    firstName?: string; 
    lastName?: string; 
    role?: 'student' | 'instructor' | 'admin';
    phone_number?: string;
    address?: string;
    organization?: string;
    job_title?: string;
    bio?: string;
  }) {
    console.log('🔄 Supabase signUp 시작:', { email, metadata });
    
    // 먼저 이메일 중복 확인
    console.log('🔍 이메일 중복 확인 중...');
    const { data: existingProfiles, error: checkError } = await supabase
      .from('user_profiles')
      .select('id, email')
      .eq('email', email)
      .limit(1);
    
    if (checkError) {
      console.error('❌ 이메일 중복 확인 중 오류:', checkError);
    } else if (existingProfiles && existingProfiles.length > 0) {
      console.log('❌ 이메일이 이미 존재함:', existingProfiles[0]);
      const duplicateError = new Error('이미 사용 중인 이메일입니다.');
      return { data: null, error: duplicateError };
    } else {
      console.log('✅ 이메일 중복 확인 완료 - 사용 가능');
    }
    
    const { data, error } = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: {
          first_name: metadata?.firstName,
          last_name: metadata?.lastName,
          role: metadata?.role || 'student',
          phone_number: metadata?.phone_number,
          address: metadata?.address,
          organization: metadata?.organization,
          job_title: metadata?.job_title,
          bio: metadata?.bio,
        }
      }
    })
    
    if (error) {
      console.error('❌ Supabase signUp 실패:', error);
    } else {
      console.log('✅ Supabase signUp 성공:', data);
      
      // 프로필 생성 확인 및 role 업데이트
      if (data.user?.id) {
        console.log('🔍 사용자 생성됨, 프로필 생성 대기...');
        
        // 프로필이 생성될 때까지 잠시 대기 후 role 업데이트
        setTimeout(async () => {
          try {
            // 먼저 프로필이 존재하는지 확인
            const { data: profile, error: profileError } = await supabase
              .from('user_profiles')
              .select('*')
              .eq('id', data.user!.id)
              .single();
            
            if (profileError) {
              console.error('❌ 프로필 조회 실패:', profileError);
            } else {
              console.log('✅ 프로필 생성 확인됨:', profile);
              
              // 프로필 정보 업데이트
              const updateData: any = {};
              
              const desiredRole = metadata?.role || 'student';
              if (profile.role !== desiredRole) {
                updateData.role = desiredRole;
              }
              
              // 추가 정보 업데이트
              if (metadata?.phone_number && profile.phone_number !== metadata.phone_number) {
                updateData.phone_number = metadata.phone_number;
              }
              if (metadata?.address && profile.address !== metadata.address) {
                updateData.address = metadata.address;
              }
              if (metadata?.organization && profile.organization !== metadata.organization) {
                updateData.organization = metadata.organization;
              }
              if (metadata?.job_title && profile.job_title !== metadata.job_title) {
                updateData.job_title = metadata.job_title;
              }
              if (metadata?.bio && profile.bio !== metadata.bio) {
                updateData.bio = metadata.bio;
              }
              
              // 업데이트할 내용이 있으면 실행
              if (Object.keys(updateData).length > 0) {
                updateData.updated_at = new Date().toISOString();
                
                const { data: updatedProfile, error: updateError } = await supabase
                  .from('user_profiles')
                  .update(updateData)
                  .eq('id', data.user!.id)
                  .select()
                  .single();
                
                if (updateError) {
                  console.error('❌ 프로필 업데이트 실패:', updateError);
                } else {
                  console.log('✅ 프로필 업데이트 성공:', updatedProfile);
                }
              }
            }
          } catch (err) {
            console.error('❌ 프로필 확인 중 오류:', err);
          }
        }, 2000); // 2초 후 확인
      }
    }
    
    return { data, error }
  },

  // 로그아웃
  async signOut() {
    const { error } = await supabase.auth.signOut()
    return { error }
  },

  // 현재 사용자 정보 가져오기
  async getCurrentUser() {
    const { data: { user } } = await supabase.auth.getUser()
    return user
  },

  // 세션 정보 가져오기
  async getSession() {
    const { data: { session } } = await supabase.auth.getSession()
    return session
  },

  // 인증 상태 변화 리스너
  onAuthStateChange(callback: (event: string, session: any) => void) {
    return supabase.auth.onAuthStateChange(callback)
  }
}

// 사용자 프로필 관련 API
export const userApi = {
  // 프로필 조회
  async getProfile(userId: string): Promise<{ data: UserProfile | null; error: any }> {
    const { data, error } = await supabase
      .from('user_profiles')
      .select('*')
      .eq('id', userId)
      .single()
    
    return { data, error }
  },

  // 프로필 업데이트
  async updateProfile(userId: string, updates: Partial<UserProfile>) {
    const { data, error } = await supabase
      .from('user_profiles')
      .update(updates)
      .eq('id', userId)
      .select()
      .single()
    
    return { data, error }
  },

  // 모든 사용자 목록 (관리자용)
  async getAllUsers() {
    const { data, error } = await supabase
      .from('user_profiles')
      .select('*')
      .order('created_at', { ascending: false })
    
    return { data, error }
  }
}

// 코스 관련 API
export const courseApi = {
  // 모든 공개 코스 조회
  async getCourses(filters?: { category?: string; level?: string; search?: string; includeAll?: boolean }) {
    try {
      console.log('🔍 Fetching courses from Supabase...');
      console.log('🔍 Supabase filters:', filters);
      
      let query = supabase
        .from('courses')
        .select(`
          *,
          instructor:user_profiles!courses_instructor_id_fkey(first_name, last_name, email),
          category:categories(name)
        `)
      
      // includeAll이 true가 아니면 published 상태만 조회
      if (!filters?.includeAll) {
        console.log('🔍 Adding published filter (non-admin user)');
        query = query.eq('status', 'published')
      } else {
        console.log('🔍 Including ALL courses (admin user)');
      }

      if (filters?.category) {
        query = query.eq('category_id', filters.category)
      }
      
      if (filters?.level) {
        query = query.eq('level', filters.level)
      }
      
      if (filters?.search) {
        query = query.or(`title.ilike.%${filters.search}%,description.ilike.%${filters.search}%`)
      }

      console.log('🔍 Executing Supabase query...');
      const { data, error } = await query.order('created_at', { ascending: false })
      
      if (error) {
        console.error('❌ Supabase courses error:', error);
        console.error('❌ Error details:', error.message, error.details, error.hint);
      } else {
        console.log('✅ Courses fetched successfully:', data?.length || 0, 'courses');
        if (data && data.length > 0) {
          console.log('📋 First course from Supabase:', data[0]);
          data.forEach((course: any, index: number) => {
            console.log(`📚 Supabase Course ${index + 1}: "${course.title}" - Status: "${course.status}"`);
          });
        } else {
          console.log('⚠️ No courses returned from Supabase');
        }
      }
      
      return { data, error }
    } catch (err) {
      console.error('❌ Network error fetching courses:', err);
      return { data: null, error: err }
    }
  },

  // 특정 코스 상세 조회
  async getCourse(courseId: string) {
    const { data, error } = await supabase
      .from('courses')
      .select(`
        *,
        instructor:user_profiles!courses_instructor_id_fkey(first_name, last_name, email, bio),
        category:categories(name),
        modules(
          *,
          chapters(*)
        )
      `)
      .eq('id', courseId)
      .single()
    
    return { data, error }
  },

  // 코스 생성 (강사용)
  async createCourse(course: Database['public']['Tables']['courses']['Insert']) {
    const { data, error } = await supabase
      .from('courses')
      .insert(course)
      .select()
      .single()
    
    return { data, error }
  },

  // 코스 업데이트 (강사용)
  async updateCourse(courseId: string, updates: Database['public']['Tables']['courses']['Update']) {
    const { data, error } = await supabase
      .from('courses')
      .update(updates)
      .eq('id', courseId)
      .select()
      .single()
    
    return { data, error }
  },

  // 강사의 코스 목록
  async getInstructorCourses(instructorId: string) {
    const { data, error } = await supabase
      .from('courses')
      .select('*')
      .eq('instructor_id', instructorId)
      .order('created_at', { ascending: false })
    
    return { data, error }
  }
}

// 수강등록 관련 API
export const enrollmentApi = {
  // 코스 수강등록
  async enrollCourse(userId: string, courseId: string) {
    const { data, error } = await supabase
      .from('enrollments')
      .insert({
        user_id: userId,
        course_id: courseId,
        status: 'active'
      })
      .select()
      .single()
    
    return { data, error }
  },

  // 사용자의 수강 목록
  async getUserEnrollments(userId: string) {
    const { data, error } = await supabase
      .from('enrollments')
      .select(`
        *,
        course:courses(
          *,
          instructor:user_profiles!courses_instructor_id_fkey(first_name, last_name)
        )
      `)
      .eq('user_id', userId)
      .order('enrollment_date', { ascending: false })
    
    return { data, error }
  },

  // 수강등록 상태 확인
  async checkEnrollment(userId: string, courseId: string) {
    const { data, error } = await supabase
      .from('enrollments')
      .select('*')
      .eq('user_id', userId)
      .eq('course_id', courseId)
      .single()
    
    return { data, error }
  },

  // 진도 업데이트
  async updateProgress(enrollmentId: string, progress: number, completedChapters: string[]) {
    const { data, error } = await supabase
      .from('enrollments')
      .update({
        progress,
        completed_chapters: completedChapters
      })
      .eq('id', enrollmentId)
      .select()
      .single()
    
    return { data, error }
  }
}

