// 구독 관련 API
export const subscriptionApi = {
  // 구독 플랜 목록
  async getSubscriptionPlans() {
    const { data, error } = await supabase
      .from('subscription_plans')
      .select('*')
      .eq('is_active', true)
      .order('sort_order')
    
    return { data, error }
  },

  // 사용자 구독 정보
  async getUserSubscription(userId: string) {
    const { data, error } = await supabase
      .from('subscriptions')
      .select(`
        *,
        plan:subscription_plans(*)
      `)
      .eq('user_id', userId)
      .single()
    
    return { data, error }
  }
}

// 파일 업로드 관련 API
export const storageApi = {
  // 이미지 업로드 (프로필, 코스 썸네일 등)
  async uploadImage(bucket: string, file: File, path: string) {
    const { data, error } = await supabase.storage
      .from(bucket)
      .upload(path, file)
    
    return { data, error }
  },

  // 파일 URL 가져오기
  getPublicUrl(bucket: string, path: string) {
    const { data } = supabase.storage
      .from(bucket)
      .getPublicUrl(path)
    
    return data.publicUrl
  },

  // 파일 삭제
  async deleteFile(bucket: string, path: string) {
    const { data, error } = await supabase.storage
      .from(bucket)
      .remove([path])
    
    return { data, error }
  }
}

// 실시간 기능
export const realtimeApi = {
  // 코스 변경사항 구독
  subscribeToCourseChanges(callback: (payload: any) => void) {
    return supabase
      .channel('courses')
      .on('postgres_changes', 
        { 
          event: '*', 
          schema: 'public', 
          table: 'courses' 
        }, 
        callback
      )
      .subscribe()
  },

  // 사용자 활동 추적
  subscribeToUserActivity(userId: string, callback: (payload: any) => void) {
    return supabase
      .channel(`user-${userId}`)
      .on('postgres_changes',
        {
          event: 'INSERT',
          schema: 'public',
          table: 'user_activities',
          filter: `user_id=eq.${userId}`
        },
        callback
      )
      .subscribe()
  }
}

// 분석 및 대시보드 API
export const analyticsApi = {
  // 사용자 활동 로그 추가
  async logUserActivity(activity: {
    user_id: string
    activity_type: string
    course_id?: string
    chapter_id?: string
    context?: Record<string, any>
  }) {
    const { data, error } = await supabase
      .from('user_activities')
      .insert(activity)
    
    return { data, error }
  },

  // 코스별 통계
  async getCourseStats(courseId: string) {
    const { data: enrollments, error: enrollmentError } = await supabase
      .from('enrollments')
      .select('status')
      .eq('course_id', courseId)

    if (enrollmentError) return { data: null, error: enrollmentError }

    const stats = {
      total_enrollments: enrollments.length,
      active_students: enrollments.filter(e => e.status === 'active').length,
      completed_students: enrollments.filter(e => e.status === 'completed').length,
      dropout_rate: 0
    }

    if (stats.total_enrollments > 0) {
      stats.dropout_rate = (enrollments.filter(e => e.status === 'dropped').length / stats.total_enrollments) * 100
    }

    return { data: stats, error: null }
  }
}

// 기본 export
export default {
  auth: authApi,
  user: userApi,
  course: courseApi,
  enrollment: enrollmentApi,
  category: categoryApi,
  subscription: subscriptionApi,
  storage: storageApi,
  realtime: realtimeApi,
  analytics: analyticsApi
}