// 레거시 Django API 클라이언트 (Supabase로 마이그레이션 중)
import axios from 'axios';
import supabaseApi, { courseApi, categoryApi, subscriptionApi } from './supabaseClient';
import { supabase } from '../lib/supabase';

// 환경별 백엔드 URL 설정
const API_BASE_URL = process.env.NODE_ENV === 'production' 
  ? 'https://api.ai-lms.example.com' // 프로덕션 URL (실제 배포시 변경 필요)
  : 'http://localhost:8000';         // 개발 환경 URL

// 마이그레이션 상태 플래그
const USE_SUPABASE = process.env.REACT_APP_USE_SUPABASE === 'true' || true; // 기본값 true

// Axios 클라이언트 (레거시 지원용)
const axiosClient = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor
axiosClient.interceptors.request.use(
  (config) => {
    if (process.env.NODE_ENV === 'development') {
      console.log(`API Request: ${config.method?.toUpperCase()} ${config.url}`, 
        config.url?.includes('login') ? '로그인 요청 데이터: ' + JSON.stringify(config.data) : '');
    }
    
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// Response interceptor
axiosClient.interceptors.response.use(
  (response) => {
    if (process.env.NODE_ENV === 'development') {
      console.log(`API Response: ${response.config.method?.toUpperCase()} ${response.config.url}`, response.status);
    }
    return response;
  },
  (error) => {
    if (process.env.NODE_ENV === 'development') {
      if (error.response) {
        console.log('API Error Response:', {
          status: error.response.status,
          data: error.response.data,
          url: error.config.url,
          method: error.config.method
        });
      } else if (error.request) {
        console.log('API Network Error:', {
          url: error.config.url, 
          method: error.config.method
        });
      } else {
        console.log('API Config Error:', (error as any)?.message || "API error");
      }
    }
    
    // 401 Unauthorized 처리
    if (error.response && error.response.status === 401) {
      if (error.config.url !== '/api/auth/login/') {
        localStorage.removeItem('token');
        window.location.href = '/login';
      }
    }
    
    return Promise.reject(error);
  }
);

// API 클라이언트 통합 래퍼
const apiClient = {
  // 인증 관련 API
  auth: {
    async login(email: string, password: string) {
      if (USE_SUPABASE) {
        const { data, error } = await supabaseApi.auth.signIn(email, password);
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data: { user: data.user, token: data.session?.access_token } };
      } else {
        const response = await axiosClient.post('/api/auth/login/', { email, password });
        return response;
      }
    },

    async register(email: string, password: string, firstName?: string, lastName?: string, role?: 'student' | 'instructor' | 'admin', additionalInfo?: {
      phone_number?: string;
      address?: string;
      organization?: string;
      job_title?: string;
      bio?: string;
    }) {
      if (USE_SUPABASE) {
        const { data, error } = await supabaseApi.auth.signUp(email, password, { 
          firstName, 
          lastName, 
          role,
          ...additionalInfo
        });
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        if (!data) {
          throw new Error("Registration failed: No data returned");
        }
        return { data: { user: data.user } };
      } else {
        const response = await axiosClient.post('/api/auth/register/', { 
          email, 
          password,
          first_name: firstName,
          last_name: lastName,
          role: role || 'student',
          ...additionalInfo
        });
        return response;
      }
    },

    async logout() {
      if (USE_SUPABASE) {
        const { error } = await supabaseApi.auth.signOut();
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return {};
      } else {
        const response = await axiosClient.post('/api/auth/logout/');
        return response;
      }
    },

    async getCurrentUser() {
      if (USE_SUPABASE) {
        const user = await supabaseApi.auth.getCurrentUser();
        if (user) {
          const { data: profile } = await supabaseApi.user.getProfile(user.id);
          return { data: { ...user, profile } };
        }
        return { data: null };
      } else {
        const response = await axiosClient.get('/api/auth/user/');
        return response;
      }
    }
  },

  // 코스 관련 API
  courses: {
    async getAll(filters?: any) {
      if (USE_SUPABASE) {
        const { data, error } = await courseApi.getCourses(filters);
        if (error) {
          throw new Error((error as any)?.message || 'Failed to fetch courses');
        }
        return { data };
      } else {
        const response = await axiosClient.get('/api/courses/', { params: filters });
        return response;
      }
    },

    async getById(id: string) {
      if (USE_SUPABASE) {
        const { data, error } = await courseApi.getCourse(id);
        if (error) {
          throw new Error((error as any)?.message || 'Failed to fetch course');
        }
        return { data };
      } else {
        const response = await axiosClient.get(`/api/courses/${id}/`);
        return response;
      }
    },

    async create(courseData: any) {
      if (USE_SUPABASE) {
        const { data, error } = await courseApi.createCourse(courseData);
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.post('/api/courses/', courseData);
        return response;
      }
    },

    async update(id: string, courseData: any) {
      if (USE_SUPABASE) {
        const { data, error } = await courseApi.updateCourse(id, courseData);
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.put(`/api/courses/${id}/`, courseData);
        return response;
      }
    }
  },

  // 수강등록 관련 API
  enrollments: {
    async getUserEnrollments(userId?: string) {
      if (USE_SUPABASE) {
        const user = userId || (await supabaseApi.auth.getCurrentUser())?.id;
        if (!user) throw new Error('User not authenticated');
        
        const { data, error } = await supabase.from('enrollments').select('*').eq('user_id', user);
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.get('/api/enrollments/');
        return response;
      }
    },

    async enrollCourse(courseId: string) {
      if (USE_SUPABASE) {
        const user = await supabaseApi.auth.getCurrentUser();
        if (!user) throw new Error('User not authenticated');
        
        const { data, error } = await supabase.from('enrollments').insert({ user_id: user.id, course_id: courseId });
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.post('/api/enrollments/', { course_id: courseId });
        return response;
      }
    }
  },

  // 카테고리 관련 API
  categories: {
    async getAll() {
      if (USE_SUPABASE) {
        const { data, error } = await categoryApi.getCategories();
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.get('/api/categories/');
        return response;
      }
    }
  },

  // 구독 관련 API
  subscriptions: {
    async getPlans() {
      if (USE_SUPABASE) {
        const { data, error } = await subscriptionApi.getSubscriptionPlans();
        if (error) {
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.get('/api/subscription-plans/');
        return response;
      }
    },

    async getUserSubscription() {
      if (USE_SUPABASE) {
        const user = await supabaseApi.auth.getCurrentUser();
        if (!user) throw new Error('User not authenticated');
        
        const { data, error } = await subscriptionApi.getUserSubscription(user.id);
        if (error && error.code !== 'PGRST116') { // PGRST116은 "not found" 에러
          throw new Error((error as any)?.message || "API error");
        }
        return { data };
      } else {
        const response = await axiosClient.get('/api/subscriptions/current/');
        return response;
      }
    }
  },

  // 레거시 Axios 클라이언트 (필요시 직접 사용)
  axios: axiosClient
};

export default apiClient;
export { apiClient, supabaseApi };