import React, { useState } from 'react';
import Button from '../common/Button';

interface StorageSetupGuideProps {
  onClose?: () => void;
}

const StorageSetupGuide: React.FC<StorageSetupGuideProps> = ({ onClose }) => {
  const [step, setStep] = useState(1);

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text);
    alert('클립보드에 복사되었습니다!');
  };

  const sqlScript = `-- ⚠️ 주의: 버킷은 먼저 수동으로 생성해야 합니다!

-- 기존 정책 삭제 (중복 오류 방지)
DROP POLICY IF EXISTS "course-videos 읽기 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-videos 업로드 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-videos 삭제 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-videos 수정 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-thumbnails 읽기 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-thumbnails 업로드 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-thumbnails 삭제 허용" ON storage.objects;
DROP POLICY IF EXISTS "course-thumbnails 수정 허용" ON storage.objects;

-- course-videos 버킷 RLS 정책
CREATE POLICY "course-videos 읽기 허용" ON storage.objects
FOR SELECT USING (bucket_id = 'course-videos');

CREATE POLICY "course-videos 업로드 허용" ON storage.objects
FOR INSERT WITH CHECK (
  bucket_id = 'course-videos' 
  AND auth.role() = 'authenticated'
);

CREATE POLICY "course-videos 삭제 허용" ON storage.objects
FOR DELETE USING (
  bucket_id = 'course-videos' 
  AND auth.uid() = owner
);

CREATE POLICY "course-videos 수정 허용" ON storage.objects
FOR UPDATE USING (
  bucket_id = 'course-videos' 
  AND auth.uid() = owner
);

-- course-thumbnails 버킷 RLS 정책  
CREATE POLICY "course-thumbnails 읽기 허용" ON storage.objects
FOR SELECT USING (bucket_id = 'course-thumbnails');

CREATE POLICY "course-thumbnails 업로드 허용" ON storage.objects
FOR INSERT WITH CHECK (
  bucket_id = 'course-thumbnails' 
  AND auth.role() = 'authenticated'
);

CREATE POLICY "course-thumbnails 삭제 허용" ON storage.objects
FOR DELETE USING (
  bucket_id = 'course-thumbnails' 
  AND auth.uid() = owner
);

CREATE POLICY "course-thumbnails 수정 허용" ON storage.objects
FOR UPDATE USING (
  bucket_id = 'course-thumbnails' 
  AND auth.uid() = owner
);

-- 완료 메시지
SELECT 'Storage RLS 정책이 성공적으로 설정되었습니다!' as message;`;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 max-w-2xl max-h-[80vh] overflow-y-auto">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold">Supabase Storage 설정 가이드</h2>
          {onClose && (
            <button
              onClick={onClose}
              className="text-gray-500 hover:text-gray-700"
            >
              ✕
            </button>
          )}
        </div>

        <div className="mb-6">
          <div className="flex space-x-2 mb-4">
            {[1, 2, 3].map((stepNum) => (
              <div
                key={stepNum}
                className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium ${
                  step >= stepNum
                    ? 'bg-indigo-600 text-white'
                    : 'bg-gray-200 text-gray-600'
                }`}
              >
                {stepNum}
              </div>
            ))}
          </div>
        </div>

        {step === 1 && (
          <div className="space-y-4">
            <h3 className="text-lg font-semibold">1단계: Storage 버킷 설정</h3>
            
            <div className="bg-blue-50 border border-blue-200 p-4 rounded-lg mb-4">
              <div className="flex items-start space-x-2">
                <span className="text-blue-600 text-xl">💡</span>
                <div>
                  <h4 className="font-medium text-blue-800">기존 chapter-files 버킷 활용</h4>
                  <p className="text-sm text-blue-700 mt-1">
                    이미 chapter-files 버킷이 있으므로 이를 활용할 수 있습니다. 
                    Private을 Public으로 변경하기만 하면 됩니다!
                  </p>
                </div>
              </div>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-medium mb-3">옵션 A: 기존 버킷 활용 (추천)</h4>
              <ol className="list-decimal list-inside space-y-2 mb-4">
                <li>Supabase 대시보드 &gt; <strong>Storage</strong></li>
                <li><strong>chapter-files</strong> 버킷 클릭</li>
                <li>우측 상단 <strong>⚙️ Settings</strong> 클릭</li>
                <li><strong>"Public bucket"</strong> 활성화 ✅</li>
                <li><strong>"Update bucket"</strong> 클릭</li>
              </ol>
              
              <h4 className="font-medium mb-3">옵션 B: 새 버킷 생성</h4>
              <ol className="list-decimal list-inside space-y-2">
                <li><strong>"New bucket"</strong> 버튼 클릭</li>
                <li>새 버킷 설정:
                  <div className="bg-white p-2 mt-2 rounded border">
                    <div>Bucket name: <code>course-videos</code></div>
                    <div>Public bucket: <strong>✅ 체크</strong></div>
                    <div>File size limit: <code>524288000</code> (500MB)</div>
                    <div>Allowed MIME types: <code>video/mp4,video/webm,video/ogg,video/avi,video/mov</code></div>
                  </div>
                </li>
              </ol>
            </div>
            <div className="flex justify-end">
              <Button onClick={() => setStep(2)} variant="primary">
                다음 단계
              </Button>
            </div>
          </div>
        )}

        {step === 2 && (
          <div className="space-y-4">
            <h3 className="text-lg font-semibold">2단계: RLS 정책 설정</h3>
            <div className="bg-yellow-50 border border-yellow-200 p-4 rounded-lg mb-4">
              <div className="flex items-start space-x-2">
                <span className="text-yellow-600 text-xl">⚠️</span>
                <div>
                  <h4 className="font-medium text-yellow-800">중요한 주의사항</h4>
                  <p className="text-sm text-yellow-700 mt-1">
                    이전에 다른 버킷을 생성한 적이 있다면 중복 오류가 발생할 수 있습니다. 
                    아래 스크립트는 기존 정책을 안전하게 삭제하고 새로 생성합니다.
                  </p>
                </div>
              </div>
            </div>
            
            <div className="bg-gray-50 p-4 rounded-lg">
              <ol className="list-decimal list-inside space-y-2">
                <li>Supabase 대시보드에서 <strong>SQL Editor</strong> 이동</li>
                <li>아래 SQL 스크립트를 복사하여 실행:</li>
                <li className="text-green-600">✅ "Storage RLS 정책이 성공적으로 설정되었습니다!" 메시지 확인</li>
              </ol>
              
              <div className="mt-4">
                <div className="flex justify-between items-center mb-2">
                  <span className="text-sm font-medium">SQL 스크립트:</span>
                  <Button
                    onClick={() => copyToClipboard(sqlScript)}
                    variant="secondary"
                    size="sm"
                  >
                    복사
                  </Button>
                </div>
                <pre className="bg-gray-900 text-green-400 p-4 rounded text-xs max-h-40 overflow-y-auto">
                  {sqlScript}
                </pre>
              </div>
            </div>
            <div className="flex justify-between">
              <Button onClick={() => setStep(1)} variant="secondary">
                이전 단계
              </Button>
              <Button onClick={() => setStep(3)} variant="primary">
                다음 단계
              </Button>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="space-y-4">
            <h3 className="text-lg font-semibold">3단계: 설정 완료 확인</h3>
            <div className="bg-green-50 p-4 rounded-lg">
              <div className="flex items-center space-x-2 mb-2">
                <span className="text-green-600">✅</span>
                <span className="font-medium">설정이 완료되었습니다!</span>
              </div>
              <p className="text-sm text-gray-700 mb-4">
                이제 동영상과 썸네일을 업로드할 수 있습니다.
              </p>
              
              <div className="bg-white p-3 rounded border">
                <h4 className="font-medium mb-2">다음 단계:</h4>
                <ol className="list-decimal list-inside space-y-1 text-sm">
                  <li>이 창을 닫고 강의 생성으로 돌아가기</li>
                  <li>비디오 파일 업로드 테스트</li>
                  <li>문제가 있으면 개발자 도구의 "Storage 디버그" 사용</li>
                </ol>
              </div>
            </div>
            
            <div className="flex justify-between">
              <Button onClick={() => setStep(2)} variant="secondary">
                이전 단계
              </Button>
              <Button 
                onClick={onClose} 
                variant="primary"
                className="bg-green-600 hover:bg-green-700"
              >
                설정 완료
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default StorageSetupGuide;