import React, { useEffect } from 'react';
import { useRoutes } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from './app/store';
import { checkSession, setAuthenticatedUser } from './features/auth/authSlice';
import { supabaseApi } from './api/apiClient';
import createRoutes from './routes';
import Chatbot from './features/chatbot/Chatbot';
import DebugPanel from './components/debug/DebugPanel';

const App: React.FC = () => {
  const dispatch = useAppDispatch();
  const { isAuthenticated, user } = useAppSelector((state) => state.auth);
  
  // 애플리케이션 시작 시 세션 복원
  useEffect(() => {
    const initializeAuth = async () => {
      console.log('🔄 인증 상태 초기화 시작...');
      
      try {
        // Supabase 세션 직접 확인
        const session = await supabaseApi.auth.getSession();
        
        console.log('🔍 Supabase session check:', session ? '✅ Found' : '❌ None');
        
        if (session?.user) {
          console.log('✅ 유효한 Supabase 세션 발견:', session.user.email);
          
          // 사용자 프로필 정보 가져오기
          try {
            const { data: profile } = await supabaseApi.user.getProfile(session.user.id);
            
            const userData = {
              id: session.user.id,
              email: session.user.email || '',
              first_name: profile?.first_name,
              last_name: profile?.last_name,
              role: (profile?.role || 'student') as 'student' | 'instructor' | 'admin',
              profile: profile || undefined
            };
            
            console.log('👤 사용자 프로필 로드됨:', userData);
            
            // Redux 상태 직접 업데이트
            dispatch(setAuthenticatedUser({
              user: userData,
              token: session.access_token
            }));
            
          } catch (profileError) {
            console.error('❌ 프로필 로드 실패:', profileError);
            // 프로필 없어도 기본 사용자 정보로 설정
            dispatch(setAuthenticatedUser({
              user: {
                id: session.user.id,
                email: session.user.email || '',
                role: 'student' as 'student' | 'instructor' | 'admin'
              },
              token: session.access_token
            }));
          }
        } else {
          console.log('❌ 유효한 세션이 없음 - localStorage 정리');
          localStorage.removeItem('token');
          localStorage.removeItem('user');
        }
      } catch (error) {
        console.error('❌ 세션 초기화 오류:', error);
        localStorage.removeItem('token');
        localStorage.removeItem('user');
      }
    };

    initializeAuth();
    
    // Supabase auth 상태 변경 리스너
    const { data: { subscription } } = supabaseApi.auth.onAuthStateChange(
      async (event, session) => {
        console.log('🔄 Supabase 인증 상태 변경:', event);
        
        if (event === 'SIGNED_IN' && session?.user) {
          console.log('✅ 사용자 로그인:', session.user.email);
          // 세션 체크 대신 직접 사용자 정보 업데이트
          try {
            const { data: profile } = await supabaseApi.user.getProfile(session.user.id);
            dispatch(setAuthenticatedUser({
              user: {
                id: session.user.id,
                email: session.user.email || '',
                first_name: profile?.first_name,
                last_name: profile?.last_name,
                role: (profile?.role || 'student') as 'student' | 'instructor' | 'admin',
                profile: profile || undefined
              },
              token: session.access_token
            }));
          } catch (error) {
            console.error('❌ 프로필 로드 실패:', error);
          }
        } else if (event === 'SIGNED_OUT') {
          console.log('👋 사용자 로그아웃');
          localStorage.removeItem('token');
          localStorage.removeItem('user');
        } else if (event === 'TOKEN_REFRESHED' && session?.user) {
          console.log('🔄 토큰 갱신됨');
          // 토큰만 업데이트
          dispatch(setAuthenticatedUser({
            user: user!, // 기존 사용자 정보 유지
            token: session.access_token
          }));
        }
      }
    );
    
    // 컴포넌트 언마운트 시 리스너 정리
    return () => {
      subscription.unsubscribe();
    };
  }, [dispatch]);
  
  // 라우트 생성
  const routes = createRoutes(isAuthenticated, user?.role || '');
  const element = useRoutes(routes);
  
  return (
    <div className="min-h-screen bg-gray-50">
      {/* 개발환경에서만 디버깅 도구 표시 */}
      {process.env.NODE_ENV === 'development' && <DebugPanel />}
      {element}
      {isAuthenticated && <Chatbot />}
    </div>
  );
};

export default App;